import common from '../../common/url.js';
var _that,serverUrl;
export default {
	data() {
		return {
			prompt: '',
			phone: '',
			verification: '',
			password: '',
			netpassword: '',
			sendMsgDisabled: false,
			rTime: 60,
			msg: '发送验证码',
			sessionId:'',
			UseReg:{
				username:'老王',
				name:'老王',
				pwd:'',
				pwdcheck:'',
				telephone:'',
				verifyCode:'',
				sessionId:'',
			}
		}
	},
	onLoad: function() {
		_that = this;
		serverUrl=common.serverUrl;
		
	},
	methods: {
		formSubmit: function(e) {
			if (this.phone.trim() == null || this.phone.trim() == '') {
				this.prompt = '手机不能为空'
			} else if (this.phone.length != 11) {
				this.prompt = '手机的长度需要为11位'
			} else if (this.verification.trim() == null || this.verification.trim() == '') {
				this.prompt = '验证码不能为空'
			} else if (this.password.trim() == null || this.password.trim() == '') {
				this.prompt = '密码不能为空'
			} else if (this.netpassword.trim() != this.password.trim()) {
				this.prompt = '两次密码不一样'
			} else {
				this.prompt = '登录成功';
				this.UseReg.pwd=this.password;
				this.UseReg.pwdcheck=this.netpassword;
				this.UseReg.telephone=this.phone;
				this.UseReg.verifyCode=this.verification;
				this.UseReg.sessionId=this.sessionId;
				console.log(this.UseReg);
				var UseReg = JSON.stringify(this.UseReg);
				console.log(UseReg);
				uni.request({
					url:serverUrl+'logonRest/checkVerificationCodeAndRegister?UseReg='+this.UseReg,
					method:'POST',
					dataType:'json',
					success:function(res){
						console.log(res)
					},fail: () => {
						
					}
				})
			}
		},
		getMsg: function() {
			// 判断只有当sendMsgDisabled=false才能发送，点击后为true,需要等待60秒才能重新获取验证码
			if (_that.sendMsgDisabled == false) {
				_that.sendMsgDisabled = true;
				
				if (this.phone.trim() == null || this.phone.trim() == '') {
					this.prompt = '手机不能为空';
					_that.sendMsgDisabled=false;
				} else if (this.phone.length != 11) {
					this.prompt = '手机的长度需要为11位';
					_that.sendMsgDisabled=false;
				}else{
					console.log(serverUrl+'logonRest/getUserRegistrationcode?phone='+_that.phone);
					uni.request({
						url:serverUrl+'logonRest/getUserRegistrationcode?phone='+_that.phone,
						dataType:'json',
						success:function(res){
							var UseReg = JSON.parse(res.data.data[0]);
							console.log(UseReg);
							_that.sessionId=UseReg.data.id;
							console.log(_that.sessionId);
							
						},
						fail:function(){
							_that.prompt = '手机号不存在';
						}
					});
					// 倒计时
					var timer = setInterval(function() {
						_that.msg = _that.rTime+' S';
						if (--_that.rTime <= 0) {
							_that.msg = '发送验证码';
							_that.sendMsgDisabled = false;
							clearInterval(timer);
						} 
					
					}, 1000);
				}	
				
			}
		}

	}
}
